.anim {
    margin: 2rem;
    margin-top: 0;
    display: flex;
}

.main {
    position: relative;
    width: 700px;
    height: 700px;
    border: cadetblue solid 3px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.menu {
    width: 20rem;
    align-self: right;
}

.container-square {
    grid-area: inner-div;
    position: relative;
}


.origin {
    position: relative;
    top: 50%;
    left: 50%;
    grid-area: inner-div;
    /* background-color: aqua; */
    z-index: 2;
}

.square {
    width: 20px;
    height: 20px;
    background: red;
    position: relative;
    grid-area: inner-div;
    /* transform-origin: 50% 50%; */
    /* left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); */
}

.container-anim {
    /* background-color: aquamarine; */
    position: relative;
    display: grid;
    width: 700px;
    height: 700px;
    /* place-items: center; */
    grid-template-areas: "inner-div";
    transform-origin: center center;
    /* background-color: aqua; */
    z-index: 1;
}

.circle-anim {
    /* background-color: blue; */
    position: relative;
    grid-area: inner-div;
    border-radius: 50%;
    border: solid 1px black;
}