.mct__about {
    /* background-color: aqua; */
    display: grid;
    grid-template-columns: 20em 10em;
    column-width: 50%;
    grid-template-rows: 2;
    font-size: 140%;
    font-family: sans-serif;
    margin-left: 1rem;
}

.mct__about div {
    border: 3px solid cadetblue;
    border-radius: 3px;
    margin: 1rem;
    margin-top: 0;
    padding: 0.5rem;
    background-color: cadetblue;
}

.mct__about-image {
    grid-row: 1;
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mct__about-text {
    grid-row: 1;
    grid-column: 1;
    text-align: center;
    max-width: 20em;
}

.mct__about-links {
    grid-row: 2;
    grid-column-start: 1;
    grid-column-end: 2;
    text-align: center;
    width: 28em;
}

