.mct__navbar {
    margin-top: 1rem;
    /* background: white; */
    width: 15rem;
    height: fit-content;
    align-items: left;
    transition: width 0.3s ease-in-out;
    position: sticky;
    background-color: white;
    /* border-bottom: solid 3px black; */
}

.mct__navbar_closed {
    margin-top: 1rem;
    background: white;
    height: fit-content;
    width: 3.5rem;
    transition: width 0.3s ease-in-out;
    background-color: white;
    /* border-bottom: solid 3px black; */
}

ul, li {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}

.mct__navbar-links_container ul {
    display: table;
    width: 180px;
    height: 100%;
    /* background-color: blue; */
}


.mct__navbar-links_container img {
    width: 2.5rem;
    height: 2.5rem;
    padding: 0.25rem;
}

.mct__navbar-links_container button {
    border: none;
    /* background-color: blue; */
    width: 2.5rem;
    height: 2.5rem;
    padding-left: 0.25rem;
}

.toggle {
    display: flex;
    align-items: center;
    background-color: cadetblue;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 0.25rem;
    border-bottom: solid 3px black;
}

.toggle img {
    height: 2rem;
    width: 2rem;
    padding-right: 0.25rem;
    padding-left: 0.25rem;
}

.inside {
    display: table; 
    width: 15rem;
    height: 60px;
    background-color: cadetblue;
}
.inside ul {
    
    width: 180px;
    /* background-color: aqua; */
}
.img {
    width: 40px;
    padding-left: 0.25em;
    padding-right: 0.25rem;
    vertical-align: middle;
    display: table-cell;
    /* background-color: blueviolet; */
}

.txt {
    width: 11.5rem;
    height: 60px;
    display: table-cell;
    vertical-align: middle;
    font-size: 120%;
    font-family: sans-serif;
    /* background-color: brown; */
}


