.carousel {
    margin: 2rem;
    margin-top: 0;
    display: flex;
}



.image-box {
    /* display: flex; */
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /* transform-origin: center;
