}

.image-box img {
    position: relative;
    overflow: hidden;
    width: 200px;
    height: 200px;
    border-radius: 50%;
}


.container {
    width: 600px;
    height: 600px;
    border: cadetblue solid 1rem;
    position: relative;
    transform-origin: center center;
    border-radius: 50%;
    animation-name: rotate;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    margin: 4rem;
}

@keyframes rotate{
    from{ transform: rotate(-360deg); }
    to{ transform: rotate(360deg); }
}

.center {
    width: 300px;
    height: 300px;
    left: 300px;
    top: 300px;
    position: absolute;
    /* background-color: aqua; */
    
}

.menu {
    display: flex;
    flex-direction: column;
    width: 20rem;
    font-size: 130%;
    border-radius: 5px;
    font-family: sans-serif;
    /* align-items: center; */
    /* justify-content: center; */
    margin-left: 4rem;
}

.menu button {
    width: 10rem;
    height: 2rem;
    margin: 1rem;
    font-size: 100%;
    border-radius: 5px;
    font-family: sans-serif;
}