.calculator {
    display: flex;
}

.calc-about {
    font-family: sans-serif;
    font-size: 120%;
}

.calc-title {
    font-family: sans-serif;
    font-size: 150%;
    color: cadetblue;
    border-bottom: solid 3px cadetblue;
}

.mct__calc {
    display: flex;
    flex-direction: column;
    background-color:cadetblue;
    border-radius: 25px;
    margin: 2rem;
    margin-top: 0;
    width: 27rem;
    height: 40rem;
    align-items: center;
    font-size: 140%;
    font-family: 'Courier New', Courier, monospace;
}

/* вся панель экрана */
.mct__calc-screen {
    background-color: white;
    margin: 1.5rem; 
    border: 2px solid wheat;
    width: 22.5rem;
    height: 15%;
    align-items: right;
}

.mct__display-inp {
    display: flex;
    align-items: right;
    border-bottom: 0.1rem dashed brown;
}

input {
    align-items: right;
    border: none;
    font-size: 100%;
    font-family: 'Courier New', Courier, monospace;
}

input:focus {
    outline: none;
}

.mct__display-out {
    border: none;
}

/* панель клавиатуры */
.mct__calc-keys {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: .8rem;
}

.mct__calc-key button {
    height: 3em;
    width: 3em; 
    background-color: wheat;
    border: 0;
    font-size: 100%;
}