.home-main {
    border: solid 3px cadetblue;
    border-radius: 5px;
    height: 5rem;
    width: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding-top: 1rem; */
    padding-left: 1.5rem;
    padding-bottom: 1.5rem;
    margin: 2rem;
    margin-top: 0;
}


.home-content {
    border: solid 3px cadetblue;
    border-radius: 5px;
    height: 24rem;
    width: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 1.5rem;
    padding-bottom: 1.5rem;
    margin: 2rem;
}

.home-author {
    border: solid 3px cadetblue;
    border-radius: 5px;
    height: 4rem;
    width: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 1.5rem;
    padding-bottom: 1.5rem;
    margin: 2rem;
}
.mct__home {
    font-family: sans-serif;
    font-size: 120%;
    display: flex;
    flex-direction: column;
}


.title-main {
    position: relative;
    width: fit-content;
    height: 10em;
    padding-left: 0.5em;
    padding-right: 0.5em;
    top: -0.5em;
    background-color: white;
    color: cadetblue;
    font-weight: bold;
}

.title-content {
    position: relative;
    width: fit-content;
    height: fit-content;
    padding-left: 0.5em;
    padding-right: 0.5em;
    top: -0.8em;
    background-color: white;
    color: cadetblue;
    font-weight: bold;
}

.title-about {
    position: relative;
    width: fit-content;
    height: fit-content;
    padding-left: 0.5em;
    padding-right: 0.5em;
    top: -0.7em;
    background-color: white;
    color: cadetblue;
    font-weight: bold;
}

.home-content li {
    list-style-type: circle;
    margin-left: 2em;
}

.home-content ul {
    padding-top: 1em;
}